.categories__slider {
  padding: 0px;
}
.categories__slider.owl-carousel {
  padding-bottom: 40px;
}
.categories__slider.owl-carousel .owl-nav button {
  font-size: 36px;
  color: #888888;
  position: absolute;
  left: -60px;
  top: 35%;
}
.categories__slider.owl-carousel{
  left: auto;
  right: -60px;
}

.categories__item {
  text-align: center;
  border-radius: 30%;
  -webkit-transition: all, 0.3s;
  -o-transition: all, 0.3s;
  transition: all, 0.3s;
  height: 100%;
  width: auto;
  padding-top: 100%;
  position: relative;
  border: 1px solid #b4b4b4;
}

.categories__item:after {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: -48px;
  width: 15px;
  height: 15px;
  border: 1px solid #f08632;
  border-color: transparent #f08632 #f08632 transparent;
  content: "";
  -webkit-transform: rotate(-100deg);
      -ms-transform: rotate(-135deg);
          transform: rotate(-135deg);
  opacity: 0;
  -webkit-transition: all, 0.3s;
  -o-transition: all, 0.3s;
  transition: all, 0.3s;
}
.categories__item:hover {
  background: #888888;
}
.categories__item:active {
  background: transparent;
}
.categories__item:hover:after {
  opacity: 1;
}
.categories__item:hover span {
  color: #ffffff;
}
.categories__item:hover h5 {
  color: #ffffff;
}

.categories__item__icon {
  display: block;
  margin-top: 20%;
}
.categories__item__icon span {
  color: #f08632;
  -webkit-transition: all, 0.2s;
  -o-transition: all, 0.2s;
  transition: all, 0.2s;
  
}
.categories__item__icon h5 {
  color: #4e4e4e;
  font-weight: 600;
  font-size: 1vw;
  text-transform: uppercase;
  margin-top: 4%;
}
@media screen and (max-width: 480px){
  .categories__item__icon h5 {
    color: #4e4e4e;
    font-weight: 100;
    font-size: 1vw;
    text-transform: uppercase;
    margin-top: 4%;
    visibility: hidden;
  }
}

.categories__item__icon div img {
    width: 50%;
    object-fit: cover;
    display: block; margin: 0px auto;
}
@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}
.categories__slider.owl-carousel .owl-nav button {
  left: -20px;
}

.categories__slider.owl-carousel .owl-nav button.owl-next {
  right: -20px;
}
.categories__slider.owl-carousel.owl-loaded.owl-drag {
  padding: 0 15px;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .categories__slider.owl-carousel .owl-nav button {
    left: 0;
  }

  .categories__slider.owl-carousel .owl-nav button.owl-next {
    right: 0;
  }
}
.categories__slider.owl-carousel .owl-nav button {
  left: -20px;
}

.categories__slider.owl-carousel .owl-nav button.owl-next {
  right: -20px;
}
.categories__item__whole {
  padding: 10px;
}
