.sidebar {
  width: 300px;
  height: 100%;
  background: #ffffff;
  position: fixed;
  top:50%;
  right: -300px;
  z-index: 3;
  transition: all .35s;
}

.menuicon:checked + label + div {
  right: 0;
}

.menuicon {
  display: none;
}

.menuicon + label {
  display: block;
  margin: 30px;
  width: 60px;
  height: 50px;
  position: relative;
  cursor: pointer;
}

.menuicon + label span {
  display: block;
  position: absolute;
  width: 100%;
  height: 5px;
  border-radius: 30px;
  background: #000000;
  transition: all .35s;
}

.menuicon + label span:nth-child(1) { top: 0; }
.menuicon + label span:nth-child(2) { top: 50%; transform: translateY(-50%); }
.menuicon + label span:nth-child(3) { bottom: 0; }
.menuicon:checked + label span {
  background: #000000;
}
.menuicon:checked + label span:nth-child(1) { top: 50%; transform:translateY(-50%) rotate(45deg); }
.menuicon:checked + label span:nth-child(2) { opacity: 0;}
.menuicon:checked + label span:nth-child(3) { bottom: 50%; transform: translateY(50%) rotate(-45deg); }

.menuicon + label {
  display: block;
  width: 60px;
  height: 50px;
  position: fixed;
  right: 0;
  top:calc(50% - 25px);
  transition: all .35s;
  cursor: pointer;
}

.menuicon:checked + label {
  z-index: 4;
  right: 300px;
}